.formations {
    .formationsContent {
        //min-height: $minHeight;
        border-radius: $borderRadius;
        width: 100%;
        overflow: hidden;
        position: relative;
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 55% 45%;
        grid-template-areas: 
            "T T"
            "B B";
        

        .formationsScolaire, .formationsPerso {
            background: $color-1;
            border-radius: $borderRadius;
            padding: .5rem;
            margin: .2rem .5rem 0 .5rem;
            box-shadow: 0 3px 10px #333;
        }
        h3 {
            font-size: 1.2rem;
        }
        .formationsScolaire {
            grid-area: T;
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: 15% 1fr;
            grid-template-areas: 
                "1"
                "2";
            h3 {
                padding-top: .5rem;
                grid-area: 1;
                color: $color-5;
            }
            .extern-link:hover {
                color: $color-5;
                font-style: bold;
            }
            .extern-link {
                color: $color-4;
                font-style: italic;
            }
            .cursus {
                justify-content: space-between;
                grid-area: 2;
                width: 100%;
                margin-left: 2%;
                display: grid;
                grid-template-columns: 1fr 1fr;
                grid-template-rows: 60% 40%;
                grid-template-areas: 
                    "L R"
                    "LL RR";
                h4 {
                    list-style: inherit;
                    span {
                        color: $color-4;
                    }
                    i {
                       color: $color-2;
                       width: 8%;
                    }
                    
                }
                ul {
                    margin-top: .5rem;
                }
                li{
                    margin-left: 2.5rem;
                    margin-bottom: .5rem;
                    span {
                        color: $white;
                    }
                    i{
                        width: 8%;
                        color: $color-5;
                    }
                }
                li:nth-child(1){
                    grid-area: L;
                }
                li:nth-child(2){
                    grid-area: R;
                }
                li:nth-child(3){
                    grid-area: LL;
                }
                li:nth-child(4){
                    grid-area: RR;
                }
            } 
        }


        .formationsPerso {
            grid-area: B;
            margin-top: 1rem;
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: 8% 47% 45%;
            grid-template-areas: 
                "W"
                "X"
                "Y";
            h3 {
                grid-area: W;
                color: $color-5;
            }
            .message {
                margin: .3rem 0 0 .5rem;
                grid-area: X;
                color: $white;
                text-align: justify;

                p:last-child {
                    margin-top: .5rem;
                }
            }
            .list-icons {
                grid-area: Y;
                display: flex;
                margin: 0 auto;
                display: inline;
                .line-icons {
                    padding-left: 2rem;
                    border: solid 1px $color-3;
                    border-radius: $borderRadius;
                    margin-top: .5rem;
                    display: flex;
                    .icon {
                        margin-right: 3rem;
                    }
                }
            }
        }
    }


    @media screen and (max-width: 850px){
        .formationContent {
          h3 {
            font-size: .8rem !important;
          }
        }
      }

    @media screen and (max-width: 690px) {
        .formationsContent {
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: 1fr;
            grid-template-areas: 
                "T"
                "B";
            
            .formationsScolaire {
                height: 100%;
                grid-area: T;
                grid-template-columns: 1fr;
                grid-template-rows: 7% 1fr;

                .cursus {
                    display: inline;
                    margin: 0 auto;

                    .formation {
                        font-size: .7rem;
                        margin: 0 0 .5rem 0;
                        border: solid 1px $color-3;
                        border-radius: $borderRadius;
                        padding: .4rem;
                    }
                }
            }
            .formationsPerso {
                font-size: .7rem;
                grid-area: B;
                display: block;
                margin-top: 1rem ;
                .list-icons {
                    .line-icons {
                        border: solid 1px $color-3;
                        border-radius: $borderRadius;
                        padding: .2rem;
                        .icon {
                            img{
                                width: 40px;
                            }
                            margin-right: .1rem;
                        }
                    }
                }
                .message p:first-child {
                    border: solid 1px $color-3;
                    border-radius: $borderRadius;
                    padding: .5rem;
                }
            }
        }
    }
}
