.home {
    .homeContent {
      background: linear-gradient(115deg, $color-1 20%, transparent 100%);
      min-height: $minHeight;
      width: 100%;
      min-height: 600px;
      border-radius: $borderRadius;
      position: relative;
      overflow: hidden;
  
      &:after {
        content: '';
        position: absolute;
        height: 100%;
        width: inherit;
        background: url('../media/zsoft.jpg') no-repeat center/cover;
        z-index: -1;
        filter: brightness(60%);
      }
  
      .content {
        position: absolute;
        top: 20%;
        left: 3%;
        font-family: $font-1;
  
        h1 {
          position: absolute;
          // left: 70%;
          top: -50%;
          
          font-size: 4rem;
          font-family: $font-3;
          color: $color-5;
          text-transform: uppercase;
          text-shadow: 3px 6px 2px $black;
        }
        h2 {
          font-size: 2.5rem;
          padding: 1.5rem 0 7rem;
          color: $color-3;
          text-shadow: 1px 4px 1px $black;
        }
        .pdf {
          a {
            font-size: 1rem;
            background: $color-3;
            padding: .5rem 1rem;
            border-radius: $borderRadius;
            box-shadow: 0 5px 1px rgb(15, 15, 15);
            transition: 0.2s ease;
  
            &:hover {
              background: $color-4;
            }
          }
        }
        .offre {
          a {
            font-size: 1rem;
            background: $color-3;
            padding: .5rem 1rem;
            border-radius: $borderRadius;
            box-shadow: 0 5px 1px rgb(15, 15, 15);
            transition: 0.2s ease;
            margin-left: 19.5rem;
  
            &:hover {
              background: $color-4;
            }
          }
        }
        .profil {
          position: absolute;
          color: $black;
          //left: 10%;
          width: 100%;
          top:130%;
          margin-right: 1.5rem;
          background-color: lavender;
          padding: .5rem;
          border: solid lightblue 3px;
          border-radius: $borderRadius;
          box-shadow: 0 0 0 1px $color-5, 0 0 0 2px $color-4, 0 0 0 3px $color-3, 0 0 0 4px $color-2;
        }
      }
    }
  }

  @media screen and (max-width: 930px){
    .content {
      position: absolute;
      top: 10%;
      left: 1%;
      font-family: $font-1;
      h1 {
        font-size: .5rem;
      }
      h2 {
        font-size: 2rem;
        padding: 1rem 0 4rem;
      }
      .profil {
        margin-left: 2rem;
        //top: 0%;
        font-size: .8rem !important;
      }
    }
  }
  @media screen and (max-width: 850px){
    .homeContent {
      margin: 0 !important;
    }
    .profil {
      margin-left: .5rem;
      //top: 30%;
      font-size: .7rem !important;
    }
  }
  @media screen and (max-width:580px){
    .homeContent {
      &:after {
      background: url('../media/zsoft.jpg') no-repeat 60% 50%/cover !important;
      }
      h1 {
        font-size: 3rem !important;
      }
      h2 {
        font-size: 1.8rem !important;
      }
      .profil {
        margin-left: .5rem;
        //top: 0%;
        font-size: .7rem !important;
      }
    }
  }